body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

$grey: #ecf0f1;
$blue: #024BAE;
$bg: url('../img/soccer.jpg');
$mobile: url('../img/soccerMobile.jpeg');

a {
  text-decoration: none;
}

/*Desktop version*/

.header {
  display: flex;
  justify-content: space-between;
  padding: 0 50px;
  border-bottom: solid 0.5px $grey;

  img {
    align-self: start;
  }

  .MuiPaper-elevation1 {
    box-shadow: none;
  }

  .burger-button {
    display: none;
  }

}

.homepage {
  height: 120vh;
  background-image: $mobile;
  background-repeat: no-repeat;
  background-size: 100%;
  text-align: center;
}

.league {
  background-image: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3%;

  .MuiPaper-elevation1 {
    padding: 10px;
    box-shadow: none;
    border-bottom: solid 0.5px $grey;
  }

  .league-logo {
    width: 5%;
    margin-bottom: 2%;
  }
  .pl-logo {
    width: 15%;
    margin-bottom: 2%;
  }
  .liga-logo {
    width: 20%;
    margin-bottom: 2%;
  }
  .seriea-logo {
    width: 8%;
    margin-bottom: 2%;
  }

  .MuiTypography-h2 {
    margin-bottom: 2%;
    color: $blue;
  }

  .grid {
    width: 60%;
    font-family: roboto;

    .table-head {
      display: flex;
      text-align: center;
      align-items: center;
      font-size: 1.2em;
      font-weight: bold;
      
      .logo {
        background-color: $grey;
        padding: 26.5px 20px;
        width: 5%;
        border-radius: 15px 0px 0px 0px;
        
      }
      .club {
        background-color: $grey;
        padding: 15px 40px;
        width: 20%;
        text-align: left;
      }
      .head {
        background-color: $grey;
        padding: 15px 10px;
        width: 10%;
      }
      .last-head {
        background-color: $grey;
        padding: 15px 10px;
        width: 10%;
        border-radius: 0px 15px 0px 0px;
      }
    }
    .table-rows {
      display: flex;
      align-items: center;
      font-size: 1em;
      text-align: center;
      border-bottom: solid 0.5px $grey;

      .logo-row {
        padding: 9px 20px;
        width: 5%;
        display: flex;
        justify-content: space-between;
        align-items: center;

        img {
          width: 100%;
        }
      }
      
      .club-row {
        padding: 15px 40px;
        width: 20%;
        text-align: left;
      }
      .first-row {
        padding: 15px 10px;
        width: 10%; 
        font-weight: bold;     
      }
      .row {
        padding: 15px 10px;
        width: 10%;      
      }
    }
  }
}


/*Tablette version*/
@media only screen and (max-width: 1400px) {
  .header {

    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 9px;

    .MuiPaper-elevation1 {
      display: none;
    }

    .burger-button{
      display: block;

      .MuiButton-containedPrimary {
        background-color: $blue;
      }
    } 
  }

  .homepage {
    background-image: $mobile;
    background-position: center;
    background-attachment: fixed;       
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    height:100vh;
    width:100%; 
  }

  .league {

    .league-logo {
      width: 15%;
      margin-bottom: 5%;
    }
    .pl-logo {
      width: 30%;
      margin-bottom: 5%;
    }
    .liga-logo {
      width: 50%;
      margin-bottom: 5%;
    }
    .seriea-logo {
      width: 20%;
      margin-bottom: 5%;
    }
    .bd-logo {
      width: 30%;
      margin-bottom: 5%;
    }

    .MuiTypography-h2 {
      margin-bottom: 2%;
      color: $blue;
      font-size: 2em;
    }

    .grid {
      width: 100%;

    }
  }
  
}

/*Iphone version*/
@media only screen and (max-width: 375px) {
  .league {

    .league-logo {
      width: 15%;
      margin-bottom: 5%;
    }
    .pl-logo {
      width: 30%;
      margin-bottom: 5%;
    }
    .liga-logo {
      width: 50%;
      margin-bottom: 5%;
    }
    .seriea-logo {
      width: 20%;
      margin-bottom: 5%;
    }
    .bd-logo {
      width: 30%;
      margin-bottom: 5%;
    }

    .MuiTypography-h2 {
      margin-bottom: 2%;
      color: $blue;
      font-size: 1.6em;
    }

    .grid {
      width: 100%;

      .table-head {
        font-size: 1em;

        .logo {
          padding: 24.5px 20px;
          
        }
      }

      .table-rows {
        font-size: 0.8em;

        .logo-row {
          padding: 2px;
          width: 20%;

          img {
            width: 15px;
          }
        }

        .club-row {
          padding: 15px 40px;
          width: 30%;
          text-align: left;
        }
      }

    }
    
  }
}
